















































































































.slab-stat-small {
  background: #fff;
  border-radius: 0.8rem;
  padding: 1rem 2rem;
  box-shadow: 0 7px 14px rgba(0, 0, 0, 0.18);
}

.gridder {
  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-column-gap: 3vw;
}
